import axios from 'axios'
import Config from './Config'
// import * as http from "node/http";
// 
axios.interceptors.request.use(function (config) {
  // 
  return config
},function (error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function (response) {
  // 
  return response
},function (error) {
  // 
  return Promise.reject(error)
});
export default function fetch(options) {
  options = options || {};
  return new Promise((resolve, reject) => {
    axios({
      method: options.method || 'get',
      url: options.url,
      baseURL:options.baseUrl || Config.baseUrl,
      header: {
        'X-Requested-With': 'XMLHttpRequest',
        'Access-Control-Allow-Origin': '*',
        "Access-Control-Allow-Methods": "POST,GET",
        'Access-Control-Allow-Credentials': true,
        'Content-Type': 'application/json;charset=UTF-8',
        'Access-Control-Expose-Headers': 'Authorization',
      },
      //`params`----URL
      //URLSearchParams
      params: options.data,
      // //`paramsSerializer`params
      // //(https://www.npmjs.com/package/qs,http://api.jquery.com/jquery.param)
      // eslint-disable-next-line no-unused-vars
      paramsSerializer: function (params) {

      },
      //`data`
      //`put/post/patch`
      //`transformRequest`dada
      //string/plain/object/ArrayBuffer/ArrayBufferView/URLSearchParams
      //FormData/File/Bold
      //node:Stream
      data: options.data,
      // `transformResponse`  then/catch 
      transformResponse: [function (data) {
        //  data 
        return data;
      }],
      timeout:2000,
      // `withCredentials` 
      withCredentials: false, // 
      // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
      responseType: 'json', // 
      // `xsrfCookieName`  xsrf token cookie
      xsrfCookieName: 'XSRF-TOKEN', // default
      // `xsrfHeaderName`  xsrf token  HTTP 
      xsrfHeaderName: 'X-XSRF-TOKEN', // 
      // `onUploadProgress` 
      // eslint-disable-next-line no-unused-vars
      onUploadProgress: function (progressEvent) {
        // 
        // console.log(progressEvent);
      },
      // `onDownloadProgress` 
      // eslint-disable-next-line no-unused-vars
      onDownloadProgress: function (progressEvent) {
        // 
        // console.log(progressEvent);
      },
      // `httpAgent`  `httpsAgent`  node.js  http  https 
      // `keepAlive` 
      // eslint-disable-next-line no-undef
      // httpAgent: new http.Agent({ keepAlive: false }),
      // eslint-disable-next-line no-undef
      // httpsAgent: new http.Agent({ keepAlive: false }),
      // `maxContentLength` 
      maxContentLength: 2000,
    }).then(response => {
      if (response.data) {
        resolve(response.data)
      }else {
        resolve(response)
      }
    }).catch(error => {
      this.$message.info('');
      reject(error)
    })
  })
}
